.widgetLg {
    flex: 2;
    padding: 20px;
    -webkit-box-shadow: 1px 1px 5px 1px #CCCCCC; 
    box-shadow: 1px 1px 5px 1px #CCCCCC;
}

.widgetLgTitle{
    font-size: 22px;
    font-weight: 600;
}

.widgetLgTable{
    width: 100%;
    border-spacing: 20px;
}

.widgetLgTh{
    text-align: left;
}

.widgetLgUser {
    display: flex;
    align-items: center;
}

.widgetLgUsername{
    font-weight: 600;
}

.widgetLgImg{
    width: 40px;
    height: 40px;
    object-fit: cover;
    border-radius: 50%;
    margin-right: 5px;
}

.widgetLgDate, .widgetLgAmt {
    font-weight: 300;
}

.widgetLgBtn{
    padding: 5px 7px;
    border: none;
    border-radius: 10px;
}

.widgetLgBtn.Approved{
    background: #e5faf2;
    color: #3bb077;
}

.widgetLgBtn.Declined{
    background: #fff0f1;
    color: #d95087;
}

.widgetLgBtn.Pending{
    background: #ebf1fe;
    color: #2a7ade;
}